<template>
  <div class='flex-column fill-flist-darker'>
    <div class='channel-description fill-flist scroll-v'>
      <span class='white-space-preserve' v-html="channel.description"></span>
    </div>
    <div class='flex-grow flex-row'>
      <div class='flex-grow channel-messages fill-flist-dark scroll-v' v-auto-scroll>
        <message class='message' v-for="(message, i) in channel.messages" v-bind="{ message }" :key="i"></message>
      </div>
      <user-list class='user-list fill-flist' :users="channel.users" :ops="channel.ops"></user-list>
    </div>
    <div class='chatbox-container fill-flist'>
      <chatbox class='chatbox' :placeholder="`Chatting as ${currentCharacter}...`" @submit='handleChatboxSubmit'></chatbox>
    </div>
  </div>
</template>

<script>
import {state, sendChannelMessage} from '../lib/store'
import {Channel} from '../lib/f-chat'
import {autoScroll} from '../lib/directives'

export default {
  props: {
    channel: Channel,
  },
  methods: {
    handleChatboxSubmit (message) {
      sendChannelMessage(this.channel.id, message)
    },
  },
  computed: {
    currentCharacter: () => state.currentCharacter,
  },
  components: {
    Chatbox: require('./Chatbox.vue'),
    Message: require('./Message.vue'),
    CharacterName: require('./CharacterName.vue'),
    UserList: require('./UserList.vue'),
  },
  directives: {
    autoScroll,
  },
}
</script>

<style scoped>
.channel-description {
  height: 5em;
  padding: 0.5em;
}

.channel-messages {}

.user-list {
  width: 12em;
}

.chatbox-container {
  height: 5em;
}

.chatbox {
  display: block;
  width: 100%;
  height: 100%;
}

@media (max-width: 900px) {
  .user-list {
    display: none;
  }
}
</style>



