<template lang="html">
  <div class='fill-flist-darker flex-row' @click="handleDataAttributes">
    <div class='actions flex-column flex-justify-space-between fill-flist-darker'>
      <div>
        <Action class='action menu-action' icon='menu' @click.native="openTabBar" />
        <Action class='action' icon='forum' @click.native="openChannelList" />
        <Action class='action' icon='people' @click.native="openCharacterBrowser" />
        <Action class='action' icon='account_circle' @click.native="openStatusForm" />
        <Action class='action' icon='settings' />
      </div>
      <div>
        <Action class='action' icon='exit_to_app' />
      </div>
    </div>

    <chat-tab-bar class='tabs tabs-desktop' v-bind="{ tabs, current: tabIndex }" @input="tabIndex = arguments[0]" @tab-close="handleTabClose"></chat-tab-bar>

    <component class='flex-grow' v-if="currentTab" :is="currentTab.view" v-bind="currentTab.props"></component>

    <overlay v-for="({ view, data }, i) of overlays" @close="overlays.pop()" :key="i">
      <chat-tab-bar v-if="view === 'chat-tab-bar'" v-bind="data" @input="handleTabBarOverlayInput"></chat-tab-bar>
      <component v-else :is="view" v-bind="data || {}" @close="overlays.pop()"></component>
    </overlay>
  </div>
</template>

<script>
import {
  state,
  fetchChannelList,
  joinChannel,
  leaveChannel,
  closePrivateChat,
} from '../lib/store'

export default {
  components: {
    Action: require('./ChatAction.vue'),
    ChannelView: require('./ChannelView.vue'),
    PrivateChatView: require('./PrivateChatView.vue'),
    ChannelList: require('./ChannelList.vue'),
    CharacterBrowser: require('./CharacterBrowser.vue'),
    CharacterMenu: require('./CharacterMenu.vue'),
    Overlay: require('./Overlay.vue'),
    ChatTabBar: require('./ChatTabBar.vue'),
  },
  data () {
    return {
      tabIndex: 0,
      overlays: [],
    }
  },
  computed: {
    tabs () {
      const channels = Object.values(state.joinedChannels).map(channel => {
        return {title: channel.title, view: 'channel-view', props: {channel}}
      })

      const privateChats = Object.values(
        state.privateChats
      ).map(privateChat => {
        return {
          title: privateChat.partner.name,
          view: 'private-chat-view',
          props: {privateChat},
        }
      })

      return channels.concat(privateChats)
    },
    currentTab () {
      return this.tabs[this.tabIndex]
    },
  },
  methods: {
    openChannelList () {
      this.overlays.push({view: 'channel-list'})
      fetchChannelList()
    },
    openCharacterBrowser () {
      this.overlays.push({view: 'character-browser'})
    },
    openStatusForm () {
      this.overlays.push({
        view: 'character-menu',
        data: {character: state.onlineCharacters[state.currentCharacter]},
      })
    },
    openTabBar () {
      this.overlays.push({
        view: 'chat-tab-bar',
        data: {
          tabs: this.tabs,
          current: this.tabIndex,
        },
      })
    },
    handleDataAttributes (event) {
      for (const el of event.path) {
        if (el.dataset) {
          if (el.dataset.character) {
            const character = state.onlineCharacters[el.dataset.character]
            if (character) {
              this.overlays.push({view: 'character-menu', data: {character}})
              event.preventDefault()
            }
          }
          if (el.dataset.channel) {
            const id = el.dataset.channel
            if (state.joinedChannels[id] == null) {
              joinChannel(id)
              event.preventDefault()
            }
          }
        }
      }
    },
    handleTabBarOverlayInput (selected) {
      this.tabIndex = selected
      this.overlays.pop()
    },
    handleTabClose (tab) {
      if (tab.view === 'channel-view') {
        leaveChannel(tab.props.channel.id)
      }
      if (tab.view === 'private-chat-view') {
        closePrivateChat(tab.props.privateChat.partner.name)
      }
    },
  },
}
</script>

<style lang="scss" scoped>
.actions {
  padding: 0.5em;
}

.tabs {
  width: 12em;
}

.action {
  display: block;
  padding: 0.5em 0.15em;
}

.menu-action {
  display: none;
}

@media (max-width: 700px) {
  .tabs-desktop {
    display: none;
  }

  .menu-action {
    display: block;
  }
}
</style>



