<template>
  <span :class="`character-name gender-${character.gender.toLowerCase()}`">
    <slot>
      <span :class="`status-dot status-dot-${character.status.toLowerCase()}`" :title="capitalize(character.status)">
        &bull;
      </span>
      {{ character.name }}
    </slot>
  </span>
</template>

<script>
import {Character} from '../lib/f-chat'
import capitalize from 'lodash/capitalize'

export default {
  props: {
    character: Character,
  },
  methods: {
    capitalize,
  },
}
</script>

<style lang="scss" scoped>
.character-name {
  font-weight: 500;
}

.gender-male        { color: hsla(220, 80%, 70%, 1) }
.gender-female      { color: hsla(10, 87%, 71%, 1) }
.gender-transgender { color: hsla(36, 94%, 62%, 1) }
.gender-cunt-boy    { color: hsla(102, 68%, 54%, 1) }
.gender-herm        { color: hsla(265, 65%, 70%, 1) }
.gender-male-herm   { color: hsla(180, 90%, 65%, 1) }
.gender-shemale     { color: hsla(300, 65%, 70%, 1) }
.gender-none        { color: hsla(60, 70%, 70%, 1) }

.status-dot {
  font-size: 1.2em;
  line-height: 0.5;
  vertical-align: middle;

  &-online { color: rgb(52, 152, 219) }
  &-looking { color: rgb(46, 204, 113) }
  &-busy { color: rgb(230, 126, 34) }
  &-away { color: rgb(241, 196, 15) }
  &-dnd { color: rgb(231, 76, 60) }
  &-idle { color: lighten(rgb(241, 196, 15), 30%) }
  &-offline { color: rgba(white, 0.5) }
}
</style>



