<template>
  <div class='tabs flex-column fill-flist-dark'>
    <a class='tab fill-flist flex-row flex-justify-space-between' href='#' v-for='(tab, i) of tabs' :class="current === i && 'tab--active'" @click="$emit('input', i)">
      <div>
        <i v-if="tab.view === 'channel-view'" class='material-icons' style='font-size: 1.25em'>public</i>
        <img v-if="tab.view === 'private-chat-view'" :src="tab.props.privateChat.partner.avatar" class='tab-avatar' />
        <span v-html="tab.title"></span>
      </div>
      <div>
        <i class='material-icons close-button' @click.stop="$emit('tab-close', tab, i)">close</i>
      </div>
    </a>
  </div>
</template>

<script>
export default {
  props: {
    tabs: Array,
    current: Number,
  },
}
</script>

<style lang="scss" scoped>
.tab {
  padding: 0.5em;
  opacity: 0.4;
  transition: 0.2s opacity;
}

.tab:hover:not(.tab--active) {
  opacity: 0.7;
}

.tab--active {
  opacity: 1;
}

.tab:focus {
  outline: none;
}

.tab-avatar {
  width: 1.25em;
  vertical-align: text-bottom;
}

.close-button {
  font-size: 1em;
  transition: 0.2s opacity;
  opacity: 0;
}

.tab:hover .close-button {
  opacity: 0.5;
}
</style>



