import * as bbc from 'bbc.js'
import * as flist from './f-list'

const tagset = {
  b: simpleTag('bbc-b'),
  i: simpleTag('bbc-i'),
  u: simpleTag('bbc-u'),
  s: simpleTag('bbc-s'),
  sup: simpleTag('bbc-sup'),
  sub: simpleTag('bbc-sub'),

  color: (text, param) => `<span class="bbc-color-${param}">${text}</span>`,

  url: (text, param) =>
    (param
      ? `<a class="bbc-url" href="${param}" target='_blank'>${text}</a>`
      : `<a class="bbc-url" href="${text}" target='_blank'>${text}</a>`),

  user: name =>
    `<a class="bbc-icon" href="${flist.getProfileURL(name)}" target='_blank'>` +
    `<img src="${flist.getAvatarURL(name)}">` +
    `</a>`,

  icon: name =>
    `<a class="bbc-icon" href="${flist.getProfileURL(name)}" target='_blank'>` +
    `<img src="${flist.getAvatarURL(name)}">` +
    `</a>`,

  eicon: icon =>
    `<img class="bbc-icon" src="https://static.f-list.net/images/eicon/${icon}.gif">`,

  channel: channel =>
    `<a href="#" class="bbc-channel" data-channel="${channel}">` +
    `<i class="material-icons icon-small">public</i> ` +
    channel +
    `</a>`,

  session: (id, title) =>
    `<a href="#" class="bbc-channel" data-channel="${id}">` +
    `<i class="material-icons icon-small">forum</i> ` +
    title +
    `</a>`,
}

function simpleTag (className) {
  return text => `<span class="${className}">${text}</span>`
}

const linkPattern = /(?:^|[^=])(https?:\/\/\S+)/gi

function renderLinks (text) {
  return text.replace(linkPattern, '[url=$1]$1[/url]')
}

export function renderBBC (input) {
  return bbc.renderBBC(renderLinks(input), tagset)
}



