<template>
  <div class='flex-column'>
    <div class='user-count fill-flist-dark'>
      {{ users.length }} characters online
    </div>
    <div class='user-list flex-grow scroll-v'>
      <a href='#' class='user flex-row flex-justify-space-between' v-for="user in sortedUsers" :data-character="user.name">
        <character-name :character="user"></character-name>
        <i v-if="isFriend(user.name)" class='material-icons icon-small faded'>favorite</i>
      </a>
    </div>
  </div>
</template>

<script>
import {state} from '../lib/store'
import sortBy from 'lodash/sortBy'

export default {
  props: {
    users: Array,
    ops: Array,
  },
  components: {
    CharacterName: require('./CharacterName.vue'),
  },
  methods: {
    sortWeight (char) {
      if (state.ignored[char.name]) return 9999

      if (state.friends[char.name]) return 0
      if (state.admins[char.name]) return 1
      if (this.ops.includes(char.name)) return 2
      if (char.status === 'looking') return 3
      if (char.status === 'online') return 4
      return 5
    },
    isFriend (name) {
      return state.friends[name]
    },
  },
  computed: {
    sortedUsers () {
      const nameSort = sortBy(this.users, user => user.name)
      const weightSort = sortBy(nameSort, this.sortWeight)
      return weightSort
    },
  },
}
</script>

<style lang="scss" scoped>
.user, .user-count {
  // display: block;
  padding: 0.2em 0.5em;
}
</style>



